@import url(https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: 'Rubik', 'Arial', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: 'Rubik', sans-serif;
  background-image: linear-gradient(120deg, #9cd6d3 0%, #9197cf 50%, #bf92cc 100%);
  /* background-image: linear-gradient(120deg, #a6c0fe 0%, #f68084 100%); */
  /* background-image: linear-gradient(120deg, #a6c1ee 0%, #fbc2eb 100%); */
  /* background-image: linear-gradient(120deg, #e0c3fc 0%, #8ec5fc 100%); */
  height: 100vh;
  width: 100vw;
  margin: 0;
  padding: 0;
}

.cover {
  height: 100vh;
  color: #FAFAFA;
  margin: auto;
}

.cover-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  height: 90vh;
  width: 90vw;
}

.glass {
  background: rgba( 255, 255, 255, 0.2);
  box-shadow: 0 10px 30px 0 rgba( 31, 38, 135, 0.2);
  backdrop-filter: blur(2px);
  -webkit-backdrop-filter: blur(3px);
  border: 2px solid rgba( 255, 255, 255, 0.18 );
  border-radius: 20px;
  height: 80%;
  width: 80%;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  animation-duration: 1000ms;
  animation-name: fadeIn;
}

.cover-text {
  padding: 2rem;
}

.intro-title {
  text-align: center;
  font-size: 64px;
  font-weight: 600;
  animation-duration: 2000ms;
  animation-name: fadeIn;
}

.intro-subtitle {
  text-align: center;
  font-size: 26px;
  font-weight: 400;
  padding: 1rem 0rem 5rem 0rem;
  width: 35vw;
  margin: auto;
  animation-duration: 3000ms;
  animation-name: fadeIn;
}

.socials {
  text-align: center;
}

.social-icon {
  color: white;
  width: 70px;
  height: auto;
  padding: 15px;
  margin-left: 1vw;
  margin-right: 1vw;
  animation-duration: 3000ms;
  animation-name: fadeIn;
}

.social-icon:hover {
  box-shadow: 0 3px 3px 0 rgba( 31, 38, 135, 0.2) inset;
  backdrop-filter: blur(2px);
  -webkit-backdrop-filter: blur(2px);
  border-radius: 50%;
}

@keyframes fadeIn {
  0% {opacity:0;}
  100% {opacity:1;}
}

@media (max-width: 768px) {
  .intro-title {
    font-size: 48px;
  }
  .intro-subtitle {
    font-size: 20px;
  }
  .social-icon {
    width: 60px;
  }
}
.navbar {
  height: 7vh;
  width: 40vw;
  background: rgba( 255, 255, 255, 0.2);
  box-shadow: 0 10px 30px 0 rgba( 31, 38, 135, 0.2);
  backdrop-filter: blur(2px);
  -webkit-backdrop-filter: blur(3px);
  border: 2px solid rgba( 255, 255, 255, 0.18 );
  border-radius: 0px 0px 50px 50px;
  margin: auto;
  top: 0;
  overflow: hidden;
  animation: 1s ease-out 0s 1 slideInFromTop;
}

.navBarLink {
  margin: 1.5vw;
  font-size: 20px;
  font-weight: 500;
  letter-spacing: 0.05em;
  color: #FAFAFA !important;
}

.navBarLink:hover {
  box-shadow: 0 5px 5px 0 rgba( 31, 38, 135, 0.2) inset;
  backdrop-filter: blur(3px);
  -webkit-backdrop-filter: blur(3px);
  border-radius: 20px;
}

@keyframes slideInFromTop {
  0% {
      transform: translateY(-100%);
  }
  100% {
      transform: translateY(0);
  }
}

@media (max-width: 768px) {
  .navbar {
    visibility: hidden;
  }
}
  
